.appheader{
  z-index: 100;
  display: flex;
  position: fixed;
  width: 100vw;
  height: 80px;
  justify-content: space-between;
  box-sizing: border-box;
  align-items: center;
  background-color: #FFFFFF;
  border-bottom: 1px solid #88ABE3;
  > *{
    margin-left: 24px;
    margin-right: 24px;
  }
}